<template>
  <div class="news-post">
    <section :class="{ 'loaded': loaded }">
      <div class="banner">
        <ExpandableImage :src="post.image" :alt="post.title" @loaded="onLoad" />
      </div>
      <!-- <img :alt="post.title" :src="post.image" @load="onLoad" /> -->
      <div class="header">
        <h1>{{ post.title }}</h1>
        <div class="right">
          <span>{{ datefns.format(post.date, 'PPPP') }}</span>
          <router-link class="link" to="/news">
            <i class="pi pi-angle-double-left" />
            Back
          </router-link>
        </div>
      </div>
      <p class="excerpt">{{ post.excerpt }}</p>
      <div v-html="toHTML(post.content)" class="content"></div>
    </section>
    <section :class="{ 'hidden': loaded }">
      <Skeleton width="100%" height="25rem" borderRadius="0.3rem" />
      <div class="header">
        <Skeleton width="45em" height="2.5em" />
        <div class="right">
          <Skeleton width="15em" height="1.5em" />
          <router-link class="link" to="/news">
            <i class="pi pi-angle-double-left" />
            Back
          </router-link>
        </div>
      </div>
      <Skeleton class="align-self-start" width="60%" height="1.2em" />
      <div class="content">
        <Skeleton width="100%" height="1.4em" style="margin-bottom: 0.5rem" />
        <Skeleton width="98%" height="1.4em" style="margin-bottom: 0.5rem" />
        <Skeleton width="99%" height="1.4em" style="margin-bottom: 0.5rem" />
        <Skeleton width="70%" height="1.4em" style="margin-bottom: 0.5rem" />
        <br>
        <Skeleton width="99%" height="1.4em" style="margin-bottom: 0.5rem" />
        <Skeleton width="97%" height="1.4em" style="margin-bottom: 0.5rem" />
        <Skeleton width="20%" height="1.4em" style="margin-bottom: 0.5rem" />
        <br>
        <Skeleton width="99%" height="1.4em" style="margin-bottom: 0.5rem" />
        <Skeleton width="45%" height="1.4em" style="margin-bottom: 0.5rem" />
      </div>
    </section>
  </div>
</template>

<script setup>
  import { ref, onBeforeMount } from 'vue';
  import { useRoute } from 'vue-router';
  import * as datefns from 'date-fns';
  import fetchPlus from '@/utilities/FetchPlus';
  import ExpandableImage from '@/components/common/ExpandableImage.vue';
  import getHost from '@/utilities/GetHost';

  const route = useRoute();
  const id = ref(route.params.id);
  const post = ref({});
  const loaded = ref(false);

  function toHTML(text) {
    return text.replace(/\n/g, '<br>');
  }

  function onLoad() {
    setTimeout(() => {
      loaded.value = true;
    }, 300);
  }

  onBeforeMount(() => {
    setTimeout(() => {
      // eslint-disable-next-line import/no-dynamic-require, global-require, @typescript-eslint/no-var-requires
      const info = require(`@/assets/demonstration/news/${id.value}/info.json`);
      // eslint-disable-next-line import/no-dynamic-require, global-require, @typescript-eslint/no-var-requires
      info.image = require(`@/assets/demonstration/news/${id.value}/image.png`);
      info.date = new Date(info.date);
      post.value = info;
    }, Math.floor(Math.random() * (7500 - 801) - 800));

    // fetchPlus(`${getHost()}/data/news/${id.value}/info`, 15).then((info) => { // TODO: Uncomment after demonstration
    //   info.image = `${getHost()}/data/news/${id.value}/image`;
    //   info.date = new Date(info.date);
    //   post.value = info;
    //   }).catch(() => {
    //     console.error('Failed to fetch post', id.value);
    //   });
  });
</script>

<style lang="scss" scoped>
  .news-post {
    min-height: 100%;
    max-height: fit-content;

    section {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 2rem;
      width: 100%;
      padding: 0 2rem;

      &:first-child {
        display: none;

        &.loaded {
          display: flex;
        }
      }

      &:last-child {
        display: flex;

        &.hidden {
          display: none;
        }
      }
    }

    .header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      h1 {
        margin: 0;
        font-size: 2em;
      }

      .right {
        display: flex;
        align-items: center;
        gap: 2rem;
      }
    } 

    p {
      text-align: left;
      width: 100%;
      margin: 0;
    }

    .excerpt {
      font-style: italic;
      font-size: 0.85em;
      color: var(--gray-600);
    }

    .content {
      width: 100%;
      font-size: 1.1em;
      text-align: left;
    }

    .link {
      display: flex;
      align-items: center;
      gap: 0.25rem;
      padding: 0.5em;
      border: 0.1em solid var(--text-color);
      border-radius: 0.5em;
      color: var(--text-color);
      text-decoration: none;
      font-size: 1.1em;

      &:hover {
        background-color: color-mix(in srgb, var(--theme-hover) 5%, transparent);
        border-color: var(--theme-hover);
        color: var(--theme-hover);
      }
    }
  }
</style>

<style lang="scss">
  .news-post .expandable-image {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 0.3rem;

    img {
      transform-origin: top;
    }
  }
</style>
